    </view>

    <!-- ÊêúÁ¥¢ÂíåÁ≠õÈÄâ -->
    <view class="search-filter">
      <view class="search-box">
        <input
          class="search-input"
          placeholder="ÊêúÁ¥¢Áº¥Ë¥πËÆ∞ÂΩï"
          v-model="searchKeyword"
          @input="onSearchInput"
        />
        <text class="search-icon">üîç</text>
      </view>

      <view class="filter-tabs">
        <view
          class="filter-tab"
          :class="{ active: currentStatus === '' }"
          @click="filterByStatus('')"
        >
