        </view>
        <view
          class="filter-tab"
          :class="{ active: currentStatus === 'paid' }"
          @click="filterByStatus('paid')"
        >
          Â∑≤Áº¥Ë¥π
        </view>
        <view
          class="filter-tab"
          :class="{ active: currentStatus === 'unpaid' }"
          @click="filterByStatus('unpaid')"
        >
          Êú™Áº¥Ë¥π
        </view>
      </view>
    </view>

    <!-- ËÆ∞ÂΩïÂàóË°® -->
    <view class="record-list">
      <view
        class="record-item"
        v-for="record in filteredRecords"
        :key="record.id"
        @click="goToDetail(record)"
      >
        <view class="record-header">
          <text class="record-title">{{ record.title }}</text>
          <text class="record-status" :class="record.status">
            {{ getStatusText(record.status) }}
          </text>
        </view>

        <view class="record-info">
          <view class="info-row">
            <text class="label">Áº¥Ë¥πÈáëÈ¢ùÔºö</text>
            <text class="amount">¬•{{ formatAmount(record.amount) }}</text>
          </view>
          <view class="info-row">
            <text class="label">Áº¥Ë¥πÊó∂Èó¥Ôºö</text>
            <text class="time">{{ formatDate(record.paymentTime) }}</text>
          </view>
          <view class="info-row">
            <text class="label">ËÆ¢ÂçïÂè∑Ôºö</text>
            <text class="order-no">{{ record.orderNo }}</text>
          </view>
        </view>

        <view class="record-footer">
          <text class="detail-text">ÁÇπÂáªÊü•ÁúãËØ¶ÊÉÖ ></text>
        </view>
      </view>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <view class="empty-state" v-if="filteredRecords.length === 0">
        <text class="empty-icon">üìã</text>
        <text class="empty-text">ÊöÇÊó†Áº¥Ë¥πËÆ∞ÂΩï</text>
