<script>
import { formatAmount } from "@/utils/payment.js";

export default {
  name: "PaymentRecord",
  data() {
    return {
      searchKeyword: "",
      currentStatus: "",
      records: [
        {
          id: 1,
          title: "2024Âπ¥Êò•Â≠£Â≠¶Ë¥π",
          amount: 500000, // 5000ÂÖÉÔºå‰ª•ÂàÜ‰∏∫Âçï‰Ωç
          status: "paid",
          paymentTime: "2024-03-15 14:30:25",
          orderNo: "PAY202403150001",
          description: "2024Âπ¥Êò•Â≠£Â≠¶ÊúüÂ≠¶Ë¥πÁº¥Á∫≥",
        },
        {
          id: 2,
          title: "2024Âπ¥‰ΩèÂÆøË¥π",
          amount: 300000, // 3000ÂÖÉ
          status: "paid",
          paymentTime: "2024-02-28 09:15:10",
          orderNo: "PAY202402280002",
          description: "2024Âπ¥Êò•Â≠£Â≠¶Êúü‰ΩèÂÆøË¥π",
        },
        {
          id: 3,
          title: "2024Âπ¥ÁßãÂ≠£Â≠¶Ë¥π",
          amount: 500000, // 5000ÂÖÉ
          status: "unpaid",
          paymentTime: null,
          orderNo: "PAY202409010003",
          description: "2024Âπ¥ÁßãÂ≠£Â≠¶ÊúüÂ≠¶Ë¥π",
        },
        {
          id: 4,
          title: "ÊïôÊùêË¥π",
          amount: 80000, // 800ÂÖÉ
          status: "paid",
          paymentTime: "2024-03-10 16:45:30",
          orderNo: "PAY202403100004",
          description: "2024Âπ¥Êò•Â≠£ÊïôÊùêË¥πÁî®",
        },
        {
          id: 5,
          title: "ÂÆûÈ™åË¥π",
          amount: 120000, // 1200ÂÖÉ
          status: "unpaid",
          paymentTime: null,
          orderNo: "PAY202403200005",
          description: "2024Âπ¥Êò•Â≠£ÂÆûÈ™åËØæÁ®ãË¥πÁî®",
        },
      ],
    };
  },
  computed: {
    filteredRecords() {
      let filtered = this.records;

      // ÊåâÁä∂ÊÄÅÁ≠õÈÄâ
      if (this.currentStatus) {
        filtered = filtered.filter(
          (record) => record.status === this.currentStatus
        );
      }

      // ÊåâÂÖ≥ÈîÆËØçÊêúÁ¥¢
      if (this.searchKeyword) {
        const keyword = this.searchKeyword.toLowerCase();
        filtered = filtered.filter(
          (record) =>
            record.title.toLowerCase().includes(keyword) ||
            record.orderNo.toLowerCase().includes(keyword) ||
            record.description.toLowerCase().includes(keyword)
        );
      }

      // ÊåâÊó∂Èó¥ÂÄíÂ∫èÊéíÂàó
      return filtered.sort((a, b) => {
        if (a.paymentTime && b.paymentTime) {
          return new Date(b.paymentTime) - new Date(a.paymentTime);
        }
        return 0;
      });
    },
  },
  methods: {
    formatAmount,

    onSearchInput() {
      // ÊêúÁ¥¢ËæìÂÖ•Â§ÑÁêÜ
    },

    filterByStatus(status) {
      this.currentStatus = status;
    },

    getStatusText(status) {
      const statusMap = {
        paid: "Â∑≤Áº¥Ë¥π",
        unpaid: "Êú™Áº¥Ë¥π",
        failed: "Áº¥Ë¥πÂ§±Ë¥•",
      };
      return statusMap[status] || "Êú™Áü•Áä∂ÊÄÅ";
    },

    formatDate(dateString) {
      if (!dateString) return "ÂæÖÁº¥Ë¥π";
      const date = new Date(dateString);
      return date.toLocaleString("zh-CN", {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
        hour: "2-digit",
        minute: "2-digit",
      });
    },

    goToDetail(record) {
      uni.navigateTo({
        url: `/pages/payment-detail/payment-detail?id=${record.id}&orderNo=${record.orderNo}`,
      });
    },
    loadPaymentRecords() {
      // ËøôÈáåÂèØ‰ª•Ë∞ÉÁî®APIËé∑ÂèñÁúüÂÆûÁöÑÁº¥Ë¥πËÆ∞ÂΩïÊï∞ÊçÆ
      // ÁõÆÂâç‰ΩøÁî®Ê®°ÊãüÊï∞ÊçÆ
      console.log("Âä†ËΩΩÁº¥Ë¥πËÆ∞ÂΩï");
    },
  },

  onLoad() {
    // È°µÈù¢Âä†ËΩΩÊó∂Ëé∑ÂèñÁº¥Ë¥πËÆ∞ÂΩï
    this.loadPaymentRecords();
  },

  onShow() {
    // È°µÈù¢ÊòæÁ§∫Êó∂Âà∑Êñ∞Êï∞ÊçÆ
    this.loadPaymentRecords();
  },
};
</script>

